.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}




body {
  height: 100vh;
  width: 100vw;
  /*background-image: url('https://www.shutterstock.com/shutterstock/photos/2159962175/display_1500/stock-photo-technical-price-graph-and-indicator-red-and-green-candlestick-chart-on-blue-theme-screen-market-2159962175.jpg');
  background-color: #161a30;*/
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  font-family: Arial, Helvetica, sans-serif;
}

.prompt-wrapper {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.login-box {
  position: absolute;
  background: #ffffff;
  max-width: 400px;
  padding: 35px 40px;
  border-radius: 5px;
  border-color: rgb(201, 202, 206);
  /*border-style: solid;*/
  border-image-slice: 100%;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 12px 40px 0px;
  box-sizing: border-box;
  color: rgb(30, 33, 42);
  /*line-height: 16px;*/
  
}

.login-header {
  text-align: center;
}

.login-header img {
  width: 47%;
  margin: 20px auto;
}

.login-body {
  color: #cccccc;
}

.nav-link {
  border-radius: 0px;
  color: black;
}

.nav-item :hover {
  border-width: 0px;
  /*border-bottom: 1px solid #0695c2 !important;*/
  color: black !important;
}

.nav-item .active {
  background-color: #ffffff !important;
  border-width: 0px;
  border-bottom: 1px solid blue !important;
  color: blue !important;
}

.nav-tabs {
  --bs-nav-tabs-border-width: 0px;
}

.login-body .tab-pane {
  padding: 10px 0 0;
}

.login-body input {
  margin: 10px 0;
  align-items: center;
  border-color: #c9cace;
  /*background-color: #32354b;*/
  border-radius: 3px;
  border-style: solid;
  border-width: 1px;
  padding: 15px;

}

.login-body p {
  margin: 10px 0 10px;
  text-align: center;
}

i {
  margin-right: 10px;
}

button {
  width: 100%;
  margin: 10px 0;
  border-radius: 3px;
  border-style: solid;
  border-width: 1px;
}

.btn {
  background-color: #1d2341;
  /*color: #635dff;*/
  border-color: #ffffff;
  padding: 4%;
  padding-top: 4.7%;
  padding-bottom: 4.7%;
}

.btn-primary {
  --bs-btn-hover-bg: #151931 !important;
  --bs-btn-hover-border-color: #1d2341;
  --bs-btn-hover-color: #ffffff;
  --bs-btn-disabled-bg: #292d45 !important;
}

.btn-secondary {
  color: black;
  background-color: white;
  border-color: #c9cace;
  
}

.f-pw {
  background-color: #ffffff !important;
  padding-left: 2px;
  margin-top: 3px;
  text-align: left !important;
  border-color: #635dff;
  color: #635dff;
  font-size: 88%;
  font-weight: 700;
  line-height: 24px;
  border-width: 0px;
  background-color: #ffffff;
}

.back-to-login {
  background-color: #ffffff !important;
  margin-top: 3px;
  text-align: left !important;
  border-color: #635dff;
  color: #635dff;
  font-size: 88%;
  font-weight: 700;
  line-height: 24px;
  border-width: 0px;
  background-color: #635dff;
}

.back-to-login .bi-reply-fill {
  font-size: 150%;
}

.resend-code {
  margin-top: 0px;
  background-color: #ffffff !important;
  border-color: #635dff;
  color: #635dff;
  font-size: 88%;
  font-weight: 700;
  line-height: 24px;
  border-width: 0px;
  width: auto;
  background-color: #ffffff;
}

.parent {
  display: flex;
}

.child {
  display: flex;
  flex: 1;
}

.child:first-child {
  margin-right: 20px;
}

#resend-label {
  background-color: #ffffff !important;
  padding-left: 2px;
  margin-top: 3px;
  font-size: 88%;
  font-weight: 700;
  line-height: 24px;
  border-width: 0px;
  background-color: #ffffff;
}

#subtitle {
  font-size: 88%;
}

#reset-sent {
  color: black;
  font-weight: 700;
}

#enter-handler {
  display: none;
}

/*
#retry-passwordless {
  display: none;
}*/

#number-error {
  display: none;
}

.captcha-challenge img {
  width: 100%;
}

