}

span.divder {
  display: block;
  position: relative;
  margin-bottom: 10px;
  text-align: center;
}

span.divder::before,
span.divder:after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  width: 23%;
  border-bottom: solid 0.5px #c9cace;
}

span.divder::before {
  left: 0;
}

span.divder::after {
  right: 0;
}